
import { NgModule } from '@angular/core'; 
import { SharedModule } from 'app/shared/shared.module';
import { CreateComponent } from './create/create.component';
import { ListComponent } from './list/list.component';
import { ActionDialogComponent } from './action-dialog/action-dialog.component';
import { NotificationEngineRoutingModule } from './notification-engine-routing.module';
import { NotificationEngineCrudComponent } from './notification-engine-crud/notification-engine-crud.component';
import { FuseSharedModule  } from '@fuse/shared.module';
import { ViewDialogComponent } from './view-dialog/view-dialog.component';
const COMPONENTS =[ NotificationEngineCrudComponent, CreateComponent, ListComponent];
const COMPONENTS_DYNAMIC =[ViewDialogComponent,ActionDialogComponent];

@NgModule({
    imports:[SharedModule, NotificationEngineRoutingModule,FuseSharedModule],
    declarations : [COMPONENTS,COMPONENTS_DYNAMIC],
    entryComponents : [COMPONENTS_DYNAMIC]
})

export class NotificationEngineModule{}